import Block from "../UI/blocks/Block";
import RedButton from "../UI/button/RedButton";


export default function Simulator({ simulator }) {
    return (
        <div className="simulator" style={{textAlign: "center"}}>
            <div style={{display: "flex"}}>
                <Block text={" " + simulator.id} style={{flex: 1}}/>
                <RedButton text={""} />
            </div>

            <Block text={": " + simulator.model} />
            <Block text={": " + simulator.type} />
            <Block text={": " + simulator.simulatorName} />
            <Block text={" : " + simulator.productionDate} />
            <Block text={"   : " + simulator.commissioningDate} />
            <Block text={" . : " + simulator.techCheckFrequency + " ."} />

            <div>
                {simulator.components.map(component => (
                    <div key={component.id}>
                        <Block text={" " + component.id} style={{backgroundColor:"aqua"}}/>
                        <Block text={":" + component.name} />
                    </div>
                ))}
            </div>

        </div>
    )
}